            transform: rotate(360deg);
  }
}

.lds-roller {
  display: block;
  margin: auto;
  position: relative;
  width: 64px;
  height: 64px; }

.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 32px 32px;
          transform-origin: 32px 32px; }

.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: gold;
  margin: -3px 0 0 -3px; }

.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s; }

.lds-roller div:nth-child(1):after {
  top: 50px;
  left: 50px; }

.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s; }

.lds-roller div:nth-child(2):after {
  top: 54px;
  left: 45px; }

.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s; }

.lds-roller div:nth-child(3):after {
  top: 57px;
  left: 39px; }

.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s; }

.lds-roller div:nth-child(4):after {
  top: 58px;
  left: 32px; }

.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s; }

.lds-roller div:nth-child(5):after {
  top: 57px;
  left: 25px; }

.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s; }

.lds-roller div:nth-child(6):after {
  top: 54px;
  left: 19px; }

.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s; }

.lds-roller div:nth-child(7):after {
  top: 50px;
  left: 14px; }

.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s; }

.lds-roller div:nth-child(8):after {
  top: 45px;
  left: 10px; }

@-webkit-keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

#button-container {
  width: 77.5%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  grid-gap: 20px 40px;
  margin: 10px auto; }

.active {
  background: radial-gradient(lightblue, aqua); }

h3,
p {
  font-size: 18px;
  font-family: "Roboto Condensed", sans-serif; }

h3 {
  font-size: 35px;
  margin: 10px 0 0 0;
  color: gold; }

.card {
  -webkit-animation: bounceAppear 450ms linear 0s 1 normal;
          animation: bounceAppear 450ms linear 0s 1 normal;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  color: white;
  background-color: #0b1013;
  box-shadow: 0 0 10px darkgrey;
  width: 250px;
  height: 300px;
  border-radius: 10px;
  padding: 15px;
  margin: auto; }

.favorite-icon {
  border: none;
  background: url(data:image/png;base64,);
  background-size: cover;
  display: block;
  margin: 0 0 0 auto;
  width: 30px;
  height: 30px; }
  .favorite-icon:hover {
    background: url(data:image/png;base64,);
    background-size: cover;
    -webkit-animation: wiggle .8s linear 0s infinite normal;
            animation: wiggle .8s linear 0s infinite normal;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }

.favorite {
  background: url(data:image/png;base64,);
  background-size: cover; }

.card-icon {
  border: 1px solid hotpink;
  width: 100px;
  height: 100px; }

.card-text-div {
  height: 240px;
  overflow: scroll; }

@-webkit-keyframes wiggle {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  30% {
    -webkit-transform: rotate(7deg);
            transform: rotate(7deg); }
  70% {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg); } }

@keyframes wiggle {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  30% {
    -webkit-transform: rotate(7deg);
            transform: rotate(7deg); }
  70% {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg); } }

@-webkit-keyframes bounceAppear {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.3);
            transform: scale(0.3); }
  50% {
    opacity: 0.9;
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  80% {
    opacity: 1;
    -webkit-transform: scale(0.89);
            transform: scale(0.89); }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes bounceAppear {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.3);
            transform: scale(0.3); }
  50% {
    opacity: 0.9;
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  80% {
    opacity: 1;
    -webkit-transform: scale(0.89);
            transform: scale(0.89); }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1); } }

#card-container {
  margin: 20px auto;
  padding: 15px;
  width: 80%;
  height: 460px;
  overflow: scroll;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  justify-content: center;
  align-content: flex-start;
  grid-gap: 20px; }

#show-button-div {
  width: 250px;
  height: 300px;
  margin: auto; }

.show-button {
  display: block;
  margin: 0 auto; }

#landing-container {
  position: absolute;
  left: 210px;
  top: 150px;
  margin: 0 auto;
  height: 500px;
  overflow: hidden;
  -webkit-transform-origin: 50% 70%;
          transform-origin: 50% 70%;
  -webkit-transform: perspective(400px) rotateX(20deg);
          transform: perspective(400px) rotateX(20deg);
  width: 70%; }

#scroll-text-container {
  -webkit-animation: scroll 120s linear infinite;
          animation: scroll 120s linear infinite;
  position: absolute; }

.landing-text {
  text-align: justify;
  font-size: 50px;
  font-family: "Roboto Condensed", sans-serif;
  color: gold; }

.right-justify {
  text-align: right; }

@-webkit-keyframes scroll {
  0% {
    top: 100%; }
  100% {
    top: -500%; } }

@keyframes scroll {
  0% {
    top: 100%; }
  100% {
    top: -500%; } }

@media (max-width: 1100px) {
  #landing-container {
    position: absolute;
    left: 150px;
    top: 200px; } }

@media (max-width: 800px) {
  #landing-container {
    position: absolute;
    left: 50px;
    top: 200px;
    width: 80%; }
  .landing-text {
    font-size: 40px; } }

@media (max-width: 520px) {
  #landing-container {
    position: absolute;
    left: 50px;
    top: 380px;
    height: 300px; }
  .landing-text {
    font-size: 30px; } }

@media (max-width: 400px) {
  #landing-container {
    position: absolute;
    left: 30px;
    top: 400px;
    height: 200px; }
  .landing-text {
    font-size: 30px; } }

